:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.container {
  background-color: #f4f4f4;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}

.activePatientContainer {
  background-color: #f4f4f4;
}

.patientBanner {
  display: flex;
}

.patientName {
  font-size: var(--cds-heading-03-font-size, 1.25rem);
  font-weight: var(--cds-heading-03-font-weight, 400);
  line-height: var(--cds-heading-03-line-height, 1.4);
  letter-spacing: var(--cds-heading-03-letter-spacing, 0);
  color: #161616;
  margin-right: .25rem;
  font-weight: 600;
}

.patientAvatar {
  border-radius: 1px;
  width: 3.5rem;
  height: 3.5rem;
  margin: 1rem;
}

.patientAvatarButton {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.patientInfo {
  flex-direction: column;
  width: 100%;
  padding: 1rem .5rem 1rem 0;
  display: flex;
}

.demographics {
  font-size: var(--cds-body-compact-02-font-size, 1rem);
  font-weight: var(--cds-body-compact-02-font-weight, 400);
  line-height: var(--cds-body-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-body-compact-02-letter-spacing, 0);
  color: #525252;
  flex-wrap: wrap;
  align-items: center;
  gap: .25rem;
  margin-top: .25rem;
  display: flex;
}

.row {
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  display: flex;
}

.flexRow, .tagsSlot {
  flex-flow: wrap;
  align-items: center;
  display: flex;
}

.tagsSlot {
  margin: 0 .5rem;
}

.identifiers {
  font-size: var(--cds-body-compact-02-font-size, 1rem);
  font-weight: var(--cds-body-compact-02-font-weight, 400);
  line-height: var(--cds-body-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-body-compact-02-letter-spacing, 0);
  color: #525252;
  flex-wrap: wrap;
  align-items: center;
  display: flex;
}

.identifier {
  align-items: center;
  display: flex;
}

.primaryIdentifier, .secondaryIdentifier {
  font-size: var(--cds-body-compact-02-font-size, 1rem);
  font-weight: var(--cds-body-compact-02-font-weight, 400);
  line-height: var(--cds-body-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-body-compact-02-letter-spacing, 0);
  align-items: center;
  gap: .25rem;
  display: flex;
}

.primaryIdentifier {
  color: #161616;
}

.primaryIdentifier .value {
  font-weight: bold;
}

.secondaryIdentifier .identifier {
  font-size: var(--cds-body-compact-02-font-size, 1rem);
  font-weight: var(--cds-body-compact-02-font-weight, 400);
  line-height: var(--cds-body-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-body-compact-02-letter-spacing, 0);
  align-self: baseline;
}

.gender {
  color: #525252;
  align-items: center;
  gap: .125rem;
  display: flex;
}

.separator {
  margin: .125rem;
}

.tag {
  font-size: inherit !important;
  margin: 0 !important;
}

.tooltipPadding {
  padding: .25rem;
}

.tooltipSmallText {
  font-size: 80%;
}

.actionsButtonText {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
  color: #0f62fe;
}

html[dir="rtl"] .demographics {
  gap: .25rem;
  display: flex;
}

html[dir="rtl"] .tag {
  margin: .25rem 0 .25rem .25rem;
}

html[dir="rtl"] .patientName {
  margin-right: unset;
  margin-left: .25rem;
}
