:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.container {
  width: auto;
  height: auto;
}

.container :global(.cds--overflow-menu__trigger) {
  min-height: 3rem;
}

.menu {
  min-width: initial;
  background-color: #f4f4f4;
  margin-right: .2rem;
  display: none;
  top: 3.125rem;
  left: auto;
  right: 0;
  box-shadow: 0 6px 6px #0000004d;
}

.show {
  display: block;
}

.overflowMenuButton {
  color: #0f62fe;
  width: auto;
  height: auto;
  padding: .875rem 1rem;
}

.overflowMenuButton:hover {
  background-color: #e8e8e8;
}
