:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.workspaceOuterContainer {
  --actionPanelOffset: 0px;
  width: var(--workspaceWidth);
  display: contents;
}

.workspaceOuterContainer.narrowWorkspace {
  --workspaceWidth: 26.25rem;
}

.workspaceOuterContainer.widerWorkspace {
  --workspaceWidth: 32.25rem;
}

.workspaceOuterContainer.extraWideWorkspace {
  --workspaceWidth: 48.25rem;
}

.workspaceOuterContainer .workspaceMiddleContainer {
  width: inherit;
  top: 0;
  bottom: 0;
  z-index: 100;
  position: absolute;
  inset-inline-end: var(--actionPanelOffset);
  overflow: hidden;
}

.workspaceOuterContainer .workspaceMiddleContainer.maximized {
  top: var(--omrs-navbar-height) !important;
  width: calc(100vw - var(--actionPanelOffset)) !important;
  inset-inline-start: 0 !important;
  inset-inline-end: var(--actionPanelOffset) !important;
  will-change: auto !important;
  transition: none !important;
  position: fixed !important;
  bottom: 0 !important;
  transform: none !important;
}

.workspaceOuterContainer .workspaceMiddleContainer.hidden {
  width: 0;
}

.workspaceOuterContainer .workspaceInnerContainer {
  height: 100%;
  width: var(--workspaceWidth);
  will-change: width;
  background-color: #fff;
  border-inline-start: 1px solid #a8a8a8;
  flex-direction: column;
  transition: width .5s ease-in-out;
  display: flex;
}

.workspaceOuterContainer .workspaceInnerContainer.maximized {
  width: 100%;
  transition: none !important;
}

.workspaceOuterContainer .workspaceSpacer {
  width: inherit;
  height: 100%;
}

.workspaceOuterContainer .workspaceSpacer.hidden {
  width: 0;
}

.loader {
  justify-content: center;
  align-items: center;
  min-height: 50%;
  display: flex;
}

.hiddenExtraWorkspace {
  display: none;
}

:global(.omrs-breakpoint-gt-tablet) .header {
  box-sizing: content-box;
  height: var(--workspace-header-height);
  background-color: #e0e0e0;
  border-bottom: 1px solid #a8a8a8;
  position: relative;
}

:global(.omrs-breakpoint-gt-tablet) .header a {
  font-size: var(--cds-heading-compact-02-font-size, 1rem);
  font-weight: var(--cds-heading-compact-02-font-weight, 600);
  line-height: var(--cds-heading-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-heading-compact-02-letter-spacing, 0);
}

:global(.omrs-breakpoint-gt-tablet) .header a:hover {
  color: inherit;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button {
  background-color: #fff;
  border-right: 1px solid #e0e0e0;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button > svg {
  fill: #121619 !important;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button:hover {
  background-color: #f4f4f4;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContent {
  background-color: #fff;
  flex-grow: 1;
  overflow-y: auto;
}

:global(.omrs-breakpoint-gt-tablet) .overlayHeaderSpacer {
  flex-grow: 1;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceOuterContainer.isActionMenuOpened {
  --actionPanelOffset: 3rem;
}

:global(.omrs-breakpoint-lt-desktop) .header {
  background-color: var(--brand-02);
  position: relative;
}

:global(.omrs-breakpoint-lt-desktop) .header a {
  color: #fff;
}

:global(.omrs-breakpoint-lt-desktop) .header button {
  color: #fff;
  background-color: var(--brand-02);
}

:global(.omrs-breakpoint-lt-desktop) .header button:hover {
  background-color: var(--brand-03);
}

:global(.omrs-breakpoint-lt-desktop) .workspaceOuterContainer {
  width: 100vw !important;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceMiddleContainer {
  width: inherit !important;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceMiddleContainer.hidden {
  display: none;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceInnerContainer {
  width: 100vw !important;
}

:global(.omrs-breakpoint-lt-desktop) .marginWorkspaceContent {
  margin-bottom: var(--bottom-nav-height);
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithActionMenu.workspaceFixedContainer {
  right: unset;
  left: var(--actionPanelOffset);
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu {
  height: 100%;
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu.workspaceFixedContainer {
  right: unset;
  left: 0;
}
