:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.container {
  color: #161616;
  border-radius: 50%;
  max-width: none;
  margin-top: .25rem;
  margin-bottom: .25rem;
}

.container:hover {
  color: #525252;
  background-color: #c6c6c6;
}

.container > span {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
  color: #525252;
  margin-top: .25rem;
}

.container svg {
  transform: scale(1.25);
}

.elementContainer {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.elementContainer .countTag, .elementContainer .interruptedTag {
  text-align: center;
  color: #f4f4f4;
  background-color: #da1e28;
  border-radius: 50%;
  padding: 0 4px;
  font-size: 10px;
  position: absolute;
  top: -14px;
  right: -12px;
}

.elementContainer .interruptedTag {
  width: 14px;
  height: 14px;
  padding: 0 5px;
  font-size: 10px;
}

:global(.omrs-breakpoint-gt-tablet) .active {
  border: .125rem solid #007d79;
  border-color: var(--brand-03);
  background-color: #fff;
  border-radius: 50%;
}

:global(.omrs-breakpoint-gt-tablet) .container svg {
  transform: scale(1.25);
}

:global(.omrs-breakpoint-lt-desktop) .container {
  border-radius: unset;
  color: #161616;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: none;
  margin-top: 0;
  margin-bottom: 0;
  display: flex;
}

:global(.omrs-breakpoint-lt-desktop) .active {
  color: #0f62fe;
  background-color: #edf5ff;
}

:global(.omrs-breakpoint-lt-desktop) .active:hover {
  color: #0f62fe;
}

:global(.omrs-breakpoint-lt-desktop) .active > span {
  color: #0f62fe;
  font-size: var(--cds-heading-compact-01-font-size, .875rem);
  font-weight: var(--cds-heading-compact-01-font-weight, 600);
  line-height: var(--cds-heading-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-heading-compact-01-letter-spacing, .16px);
}

:global(.omrs-breakpoint-lt-desktop) .active svg {
  fill: #0f62fe;
}
