.workspaceWindowsAndMenuContainer {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.workspaceWindowsAndMenuContainer.hasMaximizedWindow {
  contain: none !important;
  will-change: auto !important;
  transform: none !important;
}

.workspaceWindowsAndMenuContainer.overlay {
  contain: none;
  width: calc(100vw - var(--actionPanelOffset));
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.workspaceWindowsContainer {
  width: fit-content;
  display: grid;
}

.workspaceWindowsContainer > * {
  grid-area: 1 / 1;
}
