:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.sideRailHidden {
  display: none;
}

.sideRailVisible {
  display: flex;
}

:global(.omrs-breakpoint-gt-tablet) .sideRail {
  z-index: 100;
  width: 3rem;
  height: 100%;
}

:global(.omrs-breakpoint-gt-tablet) .sideRail.overlay {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}

:global(.omrs-breakpoint-gt-tablet) .sideRail .container {
  background: #f4f4f4;
  border-left: 1px solid #a8a8a8;
  flex-direction: column;
  align-items: center;
  width: 3rem;
  height: 100%;
  display: flex;
}

:global(.omrs-breakpoint-gt-tablet) .sideRail.withinWorkspace .container {
  top: calc(var(--omrs-navbar-height)  + var(--workspace-header-height));
}

:global(.omrs-breakpoint-lt-desktop) .sideRail {
  z-index: 8003;
  background-color: #fff;
  border-top: 1px solid #c6c6c6;
  justify-content: stretch;
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

:global(.omrs-breakpoint-lt-desktop) .chartExtensions {
  background-color: #fff;
  width: 100%;
  display: flex;
}

:global(.omrs-breakpoint-lt-desktop) .chartExtensions > div {
  cursor: pointer;
  flex: 1;
}

:global(.omrs-breakpoint-lt-desktop) .container {
  flex: 1;
  align-items: center;
  width: 100%;
  display: flex;
}

.divider {
  background-color: #a8a8a8;
  width: 2.5rem;
  height: 1px;
  margin: .75rem 0;
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .sideRail .container {
  right: unset;
  left: 0;
}
