:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.bodyCompact01, .locationRadioButton {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
}

.searchResults {
  font-size: var(--cds-body-compact-02-font-size, 1rem);
  font-weight: var(--cds-body-compact-02-font-weight, 400);
  line-height: var(--cds-body-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-body-compact-02-letter-spacing, 0);
  background-color: #f4f4f4;
  height: 25.5rem;
  max-height: 100%;
  padding: 0 1.5rem;
  overflow-y: auto;
}

.locationResultsContainer {
  padding: .5rem .125rem 0;
  display: flex;
  overflow-y: auto;
}

.locationRadioButton {
  align-items: center;
  height: 3rem;
  display: flex;
}

.locationNotFound {
  font-size: var(--cds-heading-compact-01-font-size, .875rem);
  font-weight: var(--cds-heading-compact-01-font-weight, 600);
  line-height: var(--cds-heading-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-heading-compact-01-letter-spacing, .16px);
  color: #525252;
  margin-bottom: 1rem;
}

.radioButtonSkeleton {
  margin-bottom: 2rem;
  margin-right: 0 !important;
}

.radioButtonSkeleton span {
  width: 100% !important;
}

.loadingIcon {
  justify-content: center;
  align-items: center;
  max-width: 50%;
  margin: 1rem auto;
  display: flex;
}

.loadingIcon :global(.cds--loading__stroke) {
  stroke: var(--brand-03);
}

.loadingContainer {
  margin-top: 1.5rem;
}

.emptyState {
  flex-direction: column;
  justify-content: center;
  margin: 1.5rem auto 0;
  display: flex;
}

.errorNotification {
  margin: .5rem 0;
}
