:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.desktopHeader, .tabletHeader {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: .75rem 0 .75rem 1rem;
  display: flex;
}

.desktopHeader h4:after, .tabletHeader h4:after {
  content: "";
  border-bottom: .375rem solid var(--brand-03);
  width: 2rem;
  padding-top: .188rem;
  display: block;
}

.desktopHeader {
  height: 3rem;
}

.desktopHeader h4 {
  font-size: var(--cds-heading-compact-02-font-size, 1rem);
  font-weight: var(--cds-heading-compact-02-font-weight, 600);
  line-height: var(--cds-heading-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-heading-compact-02-letter-spacing, 0);
  color: #525252;
}

.tabletHeader {
  height: 4.5rem;
}

.tabletHeader h4 {
  font-size: var(--cds-heading-03-font-size, 1.25rem);
  font-weight: var(--cds-heading-03-font-weight, 400);
  line-height: var(--cds-heading-03-line-height, 1.4);
  letter-spacing: var(--cds-heading-03-letter-spacing, 0);
  color: #525252;
}

html[dir="rtl"] .desktopHeader, html[dir="rtl"] .tabletHeader {
  text-align: right;
  padding: .75rem 1rem .75rem 0;
}
