:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.leftNav {
  border-right: 1px solid #e0e0e0;
  padding-top: 1rem;
}

.leftNav :global(.cds--side-nav__link) {
  color: #525252;
  font-size: var(--cds-heading-compact-01-font-size, .875rem);
  font-weight: var(--cds-heading-compact-01-font-weight, 600);
  line-height: var(--cds-heading-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-heading-compact-01-letter-spacing, .16px);
  padding: 0 1rem;
}

.leftNav :global(.cds--side-nav__link):focus, .leftNav :global(.cds--side-nav__link):hover {
  background-color: #f4f4f4;
}

.leftNav :global(.cds--side-nav__link):global(.active-left-nav-link) {
  color: #161616;
  border-left: .25rem solid var(--brand-01);
  background-color: #e0e0e0;
  outline: none;
  padding: 0 .75rem;
}

:global(.omrs-breakpoint-gt-tablet) :global(.cds--side-nav__link) {
  height: 2rem;
}

:global(.omrs-breakpoint-lt-desktop) :global(.cds--side-nav__link) {
  height: 3rem;
}
