:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.bodyShort01, .configurableLink {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
}

.desktop, .tablet {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
  color: #525252;
  background-color: #fff;
  border-top: 1px solid #e0e0e0;
  justify-content: space-between;
  align-items: center;
  padding-left: 1rem;
  display: flex;
}

.desktop :global(.cds--pagination) {
  border: none;
  width: auto;
  height: 2rem;
  min-height: 0;
}

.desktop :global(.cds--pagination) :global(.cds--select-input), .desktop :global(.cds--pagination) :global(.cds--btn), .desktop :global(.cds--pagination) :global(.cds--pagination__right) {
  height: 2rem;
  min-height: 0;
}

.tablet :global(.cds--pagination) {
  border: none;
  width: auto;
  height: 3rem;
  min-height: 0;
}

.tablet :global(.cds--pagination) :global(.cds--select-input), .tablet :global(.cds--pagination) :global(.cds--btn), .tablet :global(.cds--pagination) :global(.cds--pagination__right) {
  height: 3rem;
  min-height: 0;
}

.configurableLink {
  padding: 0 .5rem;
  text-decoration: none;
}

.pagination {
  font-size: var(--cds-body-compact-01-font-size, .875rem);
  font-weight: var(--cds-body-compact-01-font-weight, 400);
  line-height: var(--cds-body-compact-01-line-height, 1.28572);
  letter-spacing: var(--cds-body-compact-01-letter-spacing, .16px);
  color: #525252;
  background-color: #fff;
  display: flex;
}

div.pagination > :global(.cds--pagination__left) {
  display: none;
}
