:root {
  --brand-01: #005d5d;
  --brand-02: #004144;
  --brand-03: #007d79;
  --bottom-nav-height: 4rem;
  --workspace-header-height: 3rem;
  --tablet-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--bottom-nav-height));
  --desktop-workspace-window-height: calc(100vh - var(--omrs-navbar-height)  - var(--workspace-header-height));
}

.loader {
  background-color: #f4f4f4;
  justify-content: center;
  min-height: 3rem;
  display: flex;
}

.hiddenExtraWorkspace {
  display: none;
}

.workspaceInnerContainer {
  flex-direction: column;
  height: 100%;
  display: flex;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithActionMenu {
  height: 100%;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithActionMenu .workspaceFixedContainer {
  right: 3rem;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu {
  height: 100%;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu .workspaceFixedContainer {
  right: 0;
}

:global(.omrs-breakpoint-gt-tablet) .header {
  box-sizing: content-box;
  height: var(--workspace-header-height);
  background-color: #e0e0e0;
  border-bottom: 1px solid #a8a8a8;
  position: relative;
}

:global(.omrs-breakpoint-gt-tablet) .header a {
  font-size: var(--cds-heading-compact-02-font-size, 1rem);
  font-weight: var(--cds-heading-compact-02-font-weight, 600);
  line-height: var(--cds-heading-compact-02-line-height, 1.375);
  letter-spacing: var(--cds-heading-compact-02-letter-spacing, 0);
}

:global(.omrs-breakpoint-gt-tablet) .header a:hover {
  color: inherit;
}

:global(.omrs-breakpoint-gt-tablet) .header:not(.maximizedWindow) {
  left: auto;
  right: auto;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button {
  background-color: #fff;
  border-right: 1px solid #e0e0e0;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button > svg {
  fill: #121619 !important;
}

:global(.omrs-breakpoint-gt-tablet) .headerButtons button:hover {
  background-color: #f4f4f4;
}

:global(.omrs-breakpoint-gt-tablet) .maximizedWindow {
  width: calc(100% - 3rem) !important;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceWindowSpacer {
  height: 100%;
  transition: margin-right .5s ease-in-out;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceOverlayOuterContainer {
  z-index: 50;
  position: fixed;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceFixedContainer {
  top: var(--omrs-navbar-height);
  z-index: 50;
  background-color: #fff;
  border-left: 1px solid #a8a8a8;
  transition: width .5s ease-in-out, right .5s ease-in-out;
  position: fixed;
  bottom: 0;
}

:global(.omrs-breakpoint-gt-tablet) .extraWideWorkspace {
  width: 48.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .extraWideWorkspace .workspaceFixedContainer {
  width: 48.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .extraWideWorkspace.hiddenRelative {
  margin-right: -48.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .extraWideWorkspace .hiddenFixed {
  right: -48.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .widerWorkspace {
  width: 32.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .widerWorkspace .workspaceFixedContainer {
  width: 32.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .widerWorkspace.hiddenRelative {
  margin-right: -32.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .widerWorkspace .hiddenFixed {
  right: -32.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .narrowWorkspace {
  width: 26.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .narrowWorkspace .workspaceFixedContainer {
  width: 26.25rem;
}

:global(.omrs-breakpoint-gt-tablet) .narrowWorkspace.hiddenRelative {
  margin-right: -26.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .narrowWorkspace .hiddenFixed {
  right: -26.25rem !important;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContent {
  background-color: #fff;
  flex-grow: 1;
  overflow-y: auto;
}

:global(.omrs-breakpoint-gt-tablet) .marginWorkspaceContent {
  margin-right: 3rem;
}

:global(.omrs-breakpoint-gt-tablet) .workspaceContent > div {
  height: 100%;
}

:global(.omrs-breakpoint-gt-tablet) .overlay {
  z-index: 1000;
  background-color: #fff;
  flex-direction: column;
  display: flex;
}

:global(.omrs-breakpoint-gt-tablet) .overlayHeaderSpacer {
  flex-grow: 1;
}

:global(.omrs-breakpoint-lt-desktop) .header {
  background-color: var(--brand-02);
  position: relative;
}

:global(.omrs-breakpoint-lt-desktop) .header a {
  color: #fff;
}

:global(.omrs-breakpoint-lt-desktop) .header button {
  color: #fff;
  background-color: var(--brand-02);
}

:global(.omrs-breakpoint-lt-desktop) .header button:hover {
  background-color: var(--brand-03);
}

:global(.omrs-breakpoint-lt-desktop) .hiddenFixed {
  top: 100% !important;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceFixedContainer {
  z-index: 8002;
  transition: top .5s ease-in-out;
  position: fixed;
  inset: 0;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceContainerWithActionMenu .workspaceFixedContainer {
  bottom: var(--bottom-nav-height);
}

:global(.omrs-breakpoint-lt-desktop) .overlay {
  background-color: #fff;
  flex-direction: column;
  display: flex;
}

:global(.omrs-breakpoint-lt-desktop) .overlayCloseButton {
  order: 1;
}

:global(.omrs-breakpoint-lt-desktop) .overlayTitle {
  order: 2;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceContent {
  background-color: #f4f4f4;
  height: 100%;
  overflow-y: auto;
}

:global(.omrs-breakpoint-lt-desktop) .workspaceContent > div {
  height: 100%;
}

:global(.omrs-breakpoint-lt-desktop) .dynamicWidth {
  width: 100%;
}

:global(.omrs-breakpoint-lt-desktop) .marginWorkspaceContent {
  margin-bottom: var(--bottom-nav-height);
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithActionMenu .workspaceFixedContainer {
  right: unset;
  left: 3rem;
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu {
  height: 100%;
}

html[dir="rtl"] :global(.omrs-breakpoint-gt-tablet) .workspaceContainerWithoutActionMenu .workspaceFixedContainer {
  right: unset;
  left: 0;
}
